.order {
  box-sizing: border-box;
  margin: 16px 10px 0 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 24px;
  gap: 24px;
  background-color: #1c1c21;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.04), 0 0 2px rgba(0, 0, 0, 0.06),
    0 0 1px rgba(0, 0, 0, 0.04);
  border-radius: 40px;
  flex-grow: 0;
}
.link {
  text-decoration: none;
  color: #f2f2f3;
}
.header {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.number {
}
.price {
  display: flex;
  align-items: center;
}
.list {
  list-style-type: none;
  display: flex;
  margin: 0;
  padding: 0;
}
.item {
  display: flex;
  width: 64px;
  height: 64px;
  padding: 2px;
  background: #131316;
  border-radius: 100px;
  border: 2px solid #801ab2;
  margin-left: -16px;
  position: relative;
}
.itemLast {
}
.itemLast:before {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  background: #1c1c21;
  opacity: 0.6;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.item:nth-child(1) {
  margin-left: 0;
  z-index: 5;
}
.item:nth-child(2) {
  z-index: 4;
}
.item:nth-child(3) {
  z-index: 3;
}
.item:nth-child(4) {
  z-index: 2;
}
.item:nth-child(5) {
  z-index: 1;
}
.item:nth-child(6) {
  z-index: 0;
}
.img {
  object-fit: cover;
  object-position: center;
  max-width: 100%;
}
.rest {
  position: absolute;
  top: 24px;
  left: 24px;
  opacity: 1;
}

@media screen and (max-width: 1050px) {
  .item {
    width: 50px;
    height: 50px;
  }
}
@media screen and (max-width: 640px) {
  .title {
    font-size: 20px;
  }
  .item {
    width: 35px;
    height: 35px;
  }
}

@media screen and (max-width: 640px) {
  .item {
    width: 30px;
    height: 30px;
  }
  .number {
    font-size: 22px;
  }
  .text {
    font-size: 14px;
  }
  .rest {
    top: 20%;
    left: 30%;
  }
}
